<template>
  <div class="wrapper" ref="wrapper">
      <div class="content">
          <slot></slot>
      </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'

export default {
  props: {
    probeType:{
      type: Number,
      default: 0
    },
    pullupLoad:{
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
        scroll:null
    }
  },
  computed: {},
  mounted() {
    //  Bscroll 
      this.scroll = new Bscroll(this.$refs.wrapper,{
          observeDOM: true,
          click: true,
          probeType: this.probeType,
          pullUpLoad: this.pullupLoad,
      })
    //     
    this.scroll.on('scroll',(a)=>{
      this.$emit('scroll',a)
    })
    // 
    this.scroll.on('pullingUp', ()=>{
     this.$emit('pullingUp')
    })
  },
  methods: {
    // 
    scrollTo(x,y,time){
      this.scroll.scrollTo(x,y,time)
    },
    //   finishPullUp: pullingUp
    // 	finishPullUp()
    finishPullUp(){
      this.scroll.finishPullUp()
    },
    //  refreshdom
    //  
    refresh(){
    this.scroll && this.scroll.refresh()
    console.log('');
    },
    // Y
    getScrollY(){
      return this.scroll.y
    }
  },
  components: {
      Bscroll
  }
}
</script>

